.HPTracker--Container {
    display: -ms-flexbox;
    display: flex;
    margin-top: 4px;
}

.HPTracker--HPText {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.HPTracker--Bars {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.HPTracker--HP {
    height: 22px;
    width: 22px;
    color: black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: x-small;
    border-bottom: 1px solid black;
    border-left: 1px solid black;
}

.HPTracker--HP:nth-child(-n+10) {
    border-top: 1px solid black;
}

.HPTracker--HP:nth-child(10n) {
    border-right: 1px solid black;
}

.HPTracker--HP:last-child {
    border-right: 1px solid black;
}

.HPTracker--HP--Active {
    background-color: red;
    color: white;
}

/* more specific */
.HPTracker--HP.HPTracker--HP--White {
    border-color: white;
    color: white;
}




