.Header {
    height: 36px;
    width: 100%;
    /*background-color: rgb(235, 159, 72);*/
    background-color: white;
    position: fixed;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 1000;
}

.Header--Content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.Header--Content--Section {
    background-color: rgb(235, 159, 72);
}

.Header--EndTurnButton {
    height: 36px;
    background-color: inherit;
    -ms-flex: auto;
        flex: auto;
}

.Header--EndTurnButton--Ready {
    -webkit-animation: pulse 1.2s infinite;
            animation: pulse 1.2s infinite;
    color: black;
}

@-webkit-keyframes pulse {
    0%, 100% {
        background-color: rgb(28, 119, 255);
    }
    30%, 40% {
        background-color: rgb(30, 255, 255);
    }
}

@keyframes pulse {
    0%, 100% {
        background-color: rgb(28, 119, 255);
    }
    30%, 40% {
        background-color: rgb(30, 255, 255);
    }
}

.Header--HeightOffset {
    height: 36px;
}

.Header--TurnTracker {
    -ms-flex: auto;
        flex: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

@media (max-width: 656px) {
    .Header {
        height: 72px;
    }

    .Header--HeightOffset {
        height: 72px;
    }

    .Header--EndTurnButton {
        height: 72px;
    }
}



